import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Tabs,
  Tab,
  Box,
  Typography,
  TextField,
  Grid,
  Button,
  InputAdornment,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Alert
} from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import api from '../utils/api';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          {children}
        </Box>
      )}
    </div>
  );
}

const StudentCourses = ({ studentId }) => {
  const [value, setValue] = useState(0);
  const [courses, setCourses] = useState([]);
  const [scores, setScores] = useState([]);
  const [semesters, setSemesters] = useState([]);
  const [selectedSemester, setSelectedSemester] = useState('all');
  
  // 过滤状态
  const [coursesFilter, setCoursesFilter] = useState('');
  const [scoresFilter, setScoresFilter] = useState('');
  
  // 过滤后的数据
  const [filteredCourses, setFilteredCourses] = useState([]);
  const [filteredScores, setFilteredScores] = useState([]);

  useEffect(() => {
    fetchStudentCourses();
    fetchStudentScores();
  }, [studentId]);

  useEffect(() => {
    // 提取所有不同的学期
    if (courses.length > 0) {
      const uniqueSemesters = [...new Set(courses.map(course => course.semester))];
      setSemesters(uniqueSemesters.sort());
    }
  }, [courses]);

  useEffect(() => {
    // 过滤课程
    let filtered = [...courses];
    
    // 按学期过滤
    if (selectedSemester !== 'all') {
      filtered = filtered.filter(course => course.semester === selectedSemester);
    }
    
    // 按搜索词过滤
    if (coursesFilter) {
      const searchTerm = coursesFilter.toLowerCase();
      filtered = filtered.filter(course => 
        course.course_id.toLowerCase().includes(searchTerm) || 
        course.course_name.toLowerCase().includes(searchTerm) ||
        course.teacher_name.toLowerCase().includes(searchTerm)
      );
    }
    
    setFilteredCourses(filtered);
  }, [courses, coursesFilter, selectedSemester]);

  useEffect(() => {
    // 过滤成绩
    let filtered = [...scores];
    
    // 按学期过滤
    if (selectedSemester !== 'all') {
      filtered = filtered.filter(score => score.semester === selectedSemester);
    }
    
    // 按搜索词过滤
    if (scoresFilter) {
      const searchTerm = scoresFilter.toLowerCase();
      filtered = filtered.filter(score => 
        score.course_id.toLowerCase().includes(searchTerm) || 
        score.course_name.toLowerCase().includes(searchTerm)
      );
    }
    
    setFilteredScores(filtered);
  }, [scores, scoresFilter, selectedSemester]);

  const fetchStudentCourses = async () => {
    try {
      const response = await api.get(`/api/student/${studentId}/courses`);
      if (response.data.success) {
        setCourses(response.data.data);
        setFilteredCourses(response.data.data);
      }
    } catch (error) {
      console.error('Error fetching student courses:', error);
    }
  };

  const fetchStudentScores = async () => {
    try {
      const response = await api.get(`/api/student/${studentId}/scores`);
      if (response.data.success) {
        setScores(response.data.data);
        setFilteredScores(response.data.data);
      }
    } catch (error) {
      console.error('Error fetching student scores:', error);
    }
  };

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleSemesterChange = (event) => {
    setSelectedSemester(event.target.value);
  };

  const handleCoursesFilterChange = (event) => {
    setCoursesFilter(event.target.value);
  };

  const handleScoresFilterChange = (event) => {
    setScoresFilter(event.target.value);
  };

  const handleClearFilter = () => {
    setCoursesFilter('');
    setScoresFilter('');
    setSelectedSemester('all');
  };

  return (
    <div>
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={value} onChange={handleChange}>
          <Tab label="我的课程" />
          <Tab label="成绩查询" />
        </Tabs>
      </Box>

      <Box sx={{ mt: 3, mb: 3 }}>
        <Grid container spacing={2} alignItems="center">
          <Grid item xs={12} md={4}>
            <FormControl fullWidth>
              <InputLabel>学期</InputLabel>
              <Select
                value={selectedSemester}
                onChange={handleSemesterChange}
                label="学期"
              >
                <MenuItem value="all">全部学期</MenuItem>
                {semesters.map((semester) => (
                  <MenuItem key={semester} value={semester}>
                    {semester}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
          <Grid item xs={12} md={6}>
            <TextField
              fullWidth
              label={value === 0 ? "搜索课程" : "搜索成绩"}
              variant="outlined"
              value={value === 0 ? coursesFilter : scoresFilter}
              onChange={value === 0 ? handleCoursesFilterChange : handleScoresFilterChange}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <SearchIcon />
                  </InputAdornment>
                ),
              }}
              placeholder={value === 0 ? "输入课程代码、名称或教师" : "输入课程代码或名称"}
            />
          </Grid>
          <Grid item xs={12} md={2}>
            <Button 
              variant="outlined" 
              onClick={handleClearFilter}
              fullWidth
            >
              清除筛选
            </Button>
          </Grid>
        </Grid>
      </Box>

      <TabPanel value={value} index={0}>
        <Typography variant="h6" gutterBottom>
          我的课程
        </Typography>
        {filteredCourses.length > 0 ? (
          <TableContainer component={Paper}>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>课程代码</TableCell>
                  <TableCell>课程名称</TableCell>
                  <TableCell>学分</TableCell>
                  <TableCell>教师</TableCell>
                  <TableCell>学期</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {filteredCourses.map((course) => (
                  <TableRow key={`${course.course_id}-${course.semester}`}>
                    <TableCell>{course.course_id}</TableCell>
                    <TableCell>{course.course_name}</TableCell>
                    <TableCell>{course.credit}</TableCell>
                    <TableCell>{course.teacher_name}</TableCell>
                    <TableCell>{course.semester}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        ) : (
          <Alert severity="info">没有找到符合条件的课程记录</Alert>
        )}
      </TabPanel>

      <TabPanel value={value} index={1}>
        <Typography variant="h6" gutterBottom>
          课程成绩
        </Typography>
        {filteredScores.length > 0 ? (
          <TableContainer component={Paper}>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>课程名称</TableCell>
                  <TableCell>学期</TableCell>
                  <TableCell>平时成绩</TableCell>
                  <TableCell>期末成绩</TableCell>
                  <TableCell>总成绩</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {filteredScores.map((score) => (
                  <TableRow key={`${score.course_id}-${score.semester}`}>
                    <TableCell>{score.course_name}</TableCell>
                    <TableCell>{score.semester}</TableCell>
                    <TableCell>{score.usual_score || 'N/A'}</TableCell>
                    <TableCell>{score.final_score || 'N/A'}</TableCell>
                    <TableCell>{score.total_score || 'N/A'}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        ) : (
          <Alert severity="info">没有找到符合条件的成绩记录</Alert>
        )}
      </TabPanel>
    </div>
  );
};

export default StudentCourses; 