import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
  Box,
  Tabs,
  Tab,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  Alert,
  Snackbar,
  Grid,
  IconButton
} from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import FilterListIcon from '@mui/icons-material/FilterList';
import ClearIcon from '@mui/icons-material/Clear';
import AdminTeacherCourses from './AdminTeacherCourses';
import api from '../utils/api';

function TabPanel(props) {
  const { children, value, index, ...other } = props;
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ p: 3 }}>{children}</Box>}
    </div>
  );
}

const AdminDashboard = () => {
  const [value, setValue] = useState(0);
  const [courses, setCourses] = useState([]);
  const [filteredCourses, setFilteredCourses] = useState([]);
  const [users, setUsers] = useState([]);
  const [filteredUsers, setFilteredUsers] = useState([]);
  const [departments, setDepartments] = useState([]);
  const [openDialog, setOpenDialog] = useState(false);
  const [dialogType, setDialogType] = useState('');
  const [formData, setFormData] = useState({});
  const [message, setMessage] = useState('');
  const [openSnackbar, setOpenSnackbar] = useState(false);
  const [severity, setSeverity] = useState('success');
  const [showFilters, setShowFilters] = useState(false);
  const [courseFilters, setCourseFilters] = useState({
    course_id: '',
    course_name: '',
    dept_id: '',
    credit: ''
  });
  const [userFilters, setUserFilters] = useState({
    username: '',
    name: '',
    role: '',
    dept_id: ''
  });

  const fetchData = async () => {
    try {
      const [coursesRes, usersRes, deptsRes] = await Promise.all([
        api.get('/api/admin/courses'),
        api.get('/api/admin/users'),
        api.get('/api/admin/departments')
      ]);

      setCourses(coursesRes.data.data);
      setFilteredCourses(coursesRes.data.data);
      setUsers(usersRes.data.data);
      setFilteredUsers(usersRes.data.data);
      setDepartments(deptsRes.data.data);
    } catch (error) {
      showMessage('Error fetching data', 'error');
    }
  };

  useEffect(() => {
    fetchData();
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  // 筛选课程
  const filterCourses = () => {
    const filtered = courses.filter(course => {
      return (
        (courseFilters.course_id === '' || 
          course.course_id.toLowerCase().includes(courseFilters.course_id.toLowerCase())) &&
        (courseFilters.course_name === '' || 
          course.course_name.toLowerCase().includes(courseFilters.course_name.toLowerCase())) &&
        (courseFilters.dept_id === '' || 
          course.dept_id === courseFilters.dept_id) &&
        (courseFilters.credit === '' || 
          course.credit.toString() === courseFilters.credit.toString())
      );
    });
    setFilteredCourses(filtered);
  };

  // 筛选用户
  const filterUsers = () => {
    const filtered = users.filter(user => {
      return (
        (userFilters.username === '' || 
          user.username.toLowerCase().includes(userFilters.username.toLowerCase())) &&
        (userFilters.name === '' || 
          user.name.toLowerCase().includes(userFilters.name.toLowerCase())) &&
        (userFilters.role === '' || 
          user.role === userFilters.role) &&
        (userFilters.dept_id === '' || 
          user.dept_id === userFilters.dept_id)
      );
    });
    setFilteredUsers(filtered);
  };

  // 处理课程筛选条件变更
  const handleCourseFilterChange = (e) => {
    setCourseFilters({
      ...courseFilters,
      [e.target.name]: e.target.value
    });
  };

  // 处理用户筛选条件变更
  const handleUserFilterChange = (e) => {
    setUserFilters({
      ...userFilters,
      [e.target.name]: e.target.value
    });
  };

  // 清除筛选条件
  const clearFilters = (type) => {
    if (type === 'courses') {
      setCourseFilters({
        course_id: '',
        course_name: '',
        dept_id: '',
        credit: ''
      });
      setFilteredCourses(courses);
    } else {
      setUserFilters({
        username: '',
        name: '',
        role: '',
        dept_id: ''
      });
      setFilteredUsers(users);
    }
  };

  const handleTabChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleOpenDialog = (type, data = {}) => {
    setDialogType(type);
    setFormData(data);
    setOpenDialog(true);
  };

  const handleCloseDialog = () => {
    setOpenDialog(false);
    setFormData({});
  };

  const handleInputChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleSubmit = async () => {
    try {
      let response;
      if (dialogType === 'addCourse') {
        response = await api.post('/api/admin/courses', formData);
      } else if (dialogType === 'editCourse') {
        response = await api.put(`/api/admin/courses/${formData.course_id}`, formData);
      } else if (dialogType === 'addUser') {
        response = await api.post('/api/admin/users', formData);
      } else if (dialogType === 'editUser') {
        response = await api.put(`/api/admin/users/${formData.user_id}`, formData);
      }

      if (response.data.success) {
        showMessage('Operation successful', 'success');
        handleCloseDialog();
        fetchData();
      }
    } catch (error) {
      showMessage('Operation failed', 'error');
    }
  };

  const handleDelete = async (type, id) => {
    const confirmDelete = window.confirm(
      type === 'courses' 
        ? `确定要删除课程 ${id} 吗？删除后相关的选课记录也会被影响。` 
        : `确定要删除用户 ${id} 吗？该操作不可撤销。`
    );
    
    if (!confirmDelete) return;
    
    try {
      const response = await api.delete(`/api/admin/${type}/${id}`);
      if (response.data.success) {
        showMessage('删除成功', 'success');
        fetchData();
      }
    } catch (error) {
      let errorMsg = '删除操作失败';
      if (error.response && error.response.data && error.response.data.message) {
        errorMsg = error.response.data.message;
      }
      showMessage(errorMsg, 'error');
    }
  };

  const showMessage = (msg, sev) => {
    setMessage(msg);
    setSeverity(sev);
    setOpenSnackbar(true);
  };

  return (
    <Box sx={{ width: '100%' }}>
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={value} onChange={handleTabChange}>
          <Tab label="课程管理" />
          <Tab label="用户管理" />
          <Tab label="教师课程分配" />
        </Tabs>
      </Box>

      <TabPanel value={value} index={0}>
        <Box sx={{ mb: 2, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
          <Button variant="contained" onClick={() => handleOpenDialog('addCourse')}>
            添加新课程
          </Button>
          <Button 
            variant="outlined" 
            startIcon={<FilterListIcon />}
            onClick={() => setShowFilters(!showFilters)}
          >
            {showFilters ? '隐藏筛选' : '显示筛选'}
          </Button>
        </Box>

        {showFilters && (
          <Paper sx={{ p: 2, mb: 2 }}>
            <Typography variant="subtitle1" gutterBottom>
              课程筛选
            </Typography>
            <Grid container spacing={2} alignItems="center">
              <Grid item xs={12} sm={6} md={3}>
                <TextField
                  fullWidth
                  size="small"
                  label="课程编号"
                  name="course_id"
                  value={courseFilters.course_id}
                  onChange={handleCourseFilterChange}
                />
              </Grid>
              <Grid item xs={12} sm={6} md={3}>
                <TextField
                  fullWidth
                  size="small"
                  label="课程名称"
                  name="course_name"
                  value={courseFilters.course_name}
                  onChange={handleCourseFilterChange}
                />
              </Grid>
              <Grid item xs={12} sm={6} md={2}>
                <FormControl fullWidth size="small">
                  <InputLabel>院系</InputLabel>
                  <Select
                    name="dept_id"
                    value={courseFilters.dept_id}
                    onChange={handleCourseFilterChange}
                    label="院系"
                  >
                    <MenuItem value=""><em>全部</em></MenuItem>
                    {departments.map((dept) => (
                      <MenuItem key={dept.dept_id} value={dept.dept_id}>
                        {dept.dept_name}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>
              <Grid item xs={12} sm={6} md={2}>
                <TextField
                  fullWidth
                  size="small"
                  label="学分"
                  name="credit"
                  type="number"
                  value={courseFilters.credit}
                  onChange={handleCourseFilterChange}
                />
              </Grid>
              <Grid item xs={12} md={2}>
                <Box sx={{ display: 'flex', gap: 1 }}>
                  <Button 
                    variant="contained" 
                    color="primary" 
                    startIcon={<SearchIcon />}
                    onClick={filterCourses}
                  >
                    搜索
                  </Button>
                  <Button 
                    variant="outlined" 
                    startIcon={<ClearIcon />}
                    onClick={() => clearFilters('courses')}
                  >
                    清除
                  </Button>
                </Box>
              </Grid>
            </Grid>
          </Paper>
        )}

        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>课程编号</TableCell>
                <TableCell>课程名称</TableCell>
                <TableCell>所属院系</TableCell>
                <TableCell>学分</TableCell>
                <TableCell>操作</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {filteredCourses.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={5} align="center">
                    暂无符合条件的课程
                  </TableCell>
                </TableRow>
              ) : (
                filteredCourses.map((course) => (
                  <TableRow key={course.course_id}>
                    <TableCell>{course.course_id}</TableCell>
                    <TableCell>{course.course_name}</TableCell>
                    <TableCell>{course.dept_name}</TableCell>
                    <TableCell>{course.credit}</TableCell>
                    <TableCell>
                      <Button onClick={() => handleOpenDialog('editCourse', course)}>编辑</Button>
                      <Button color="error" onClick={() => handleDelete('courses', course.course_id)}>
                        删除
                      </Button>
                    </TableCell>
                  </TableRow>
                ))
              )}
            </TableBody>
          </Table>
        </TableContainer>
      </TabPanel>

      <TabPanel value={value} index={1}>
        <Box sx={{ mb: 2, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
          <Button variant="contained" onClick={() => handleOpenDialog('addUser')}>
            添加新用户
          </Button>
          <Button 
            variant="outlined" 
            startIcon={<FilterListIcon />}
            onClick={() => setShowFilters(!showFilters)}
          >
            {showFilters ? '隐藏筛选' : '显示筛选'}
          </Button>
        </Box>

        {showFilters && (
          <Paper sx={{ p: 2, mb: 2 }}>
            <Typography variant="subtitle1" gutterBottom>
              用户筛选
            </Typography>
            <Grid container spacing={2} alignItems="center">
              <Grid item xs={12} sm={6} md={3}>
                <TextField
                  fullWidth
                  size="small"
                  label="用户名"
                  name="username"
                  value={userFilters.username}
                  onChange={handleUserFilterChange}
                />
              </Grid>
              <Grid item xs={12} sm={6} md={3}>
                <TextField
                  fullWidth
                  size="small"
                  label="姓名"
                  name="name"
                  value={userFilters.name}
                  onChange={handleUserFilterChange}
                />
              </Grid>
              <Grid item xs={12} sm={6} md={2}>
                <FormControl fullWidth size="small">
                  <InputLabel>角色</InputLabel>
                  <Select
                    name="role"
                    value={userFilters.role}
                    onChange={handleUserFilterChange}
                    label="角色"
                  >
                    <MenuItem value=""><em>全部</em></MenuItem>
                    <MenuItem value="student">学生</MenuItem>
                    <MenuItem value="teacher">教师</MenuItem>
                    <MenuItem value="admin">管理员</MenuItem>
                  </Select>
                </FormControl>
              </Grid>
              <Grid item xs={12} sm={6} md={2}>
                <FormControl fullWidth size="small">
                  <InputLabel>院系</InputLabel>
                  <Select
                    name="dept_id"
                    value={userFilters.dept_id}
                    onChange={handleUserFilterChange}
                    label="院系"
                  >
                    <MenuItem value=""><em>全部</em></MenuItem>
                    {departments.map((dept) => (
                      <MenuItem key={dept.dept_id} value={dept.dept_id}>
                        {dept.dept_name}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>
              <Grid item xs={12} md={2}>
                <Box sx={{ display: 'flex', gap: 1 }}>
                  <Button 
                    variant="contained" 
                    color="primary" 
                    startIcon={<SearchIcon />}
                    onClick={filterUsers}
                  >
                    搜索
                  </Button>
                  <Button 
                    variant="outlined" 
                    startIcon={<ClearIcon />}
                    onClick={() => clearFilters('users')}
                  >
                    清除
                  </Button>
                </Box>
              </Grid>
            </Grid>
          </Paper>
        )}

        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>用户名</TableCell>
                <TableCell>角色</TableCell>
                <TableCell>姓名</TableCell>
                <TableCell>所属院系</TableCell>
                <TableCell>操作</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {filteredUsers.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={5} align="center">
                    暂无符合条件的用户
                  </TableCell>
                </TableRow>
              ) : (
                filteredUsers.map((user) => (
                  <TableRow key={user.user_id}>
                    <TableCell>{user.username}</TableCell>
                    <TableCell>
                      {user.role === 'student' ? '学生' : 
                      user.role === 'teacher' ? '教师' : '管理员'}
                    </TableCell>
                    <TableCell>{user.name}</TableCell>
                    <TableCell>{user.dept_name}</TableCell>
                    <TableCell>
                      <Button onClick={() => handleOpenDialog('editUser', user)}>编辑</Button>
                      <Button color="error" onClick={() => handleDelete('users', user.user_id)}>
                        删除
                      </Button>
                    </TableCell>
                  </TableRow>
                ))
              )}
            </TableBody>
          </Table>
        </TableContainer>
      </TabPanel>

      <TabPanel value={value} index={2}>
        <AdminTeacherCourses />
      </TabPanel>

      <Dialog open={openDialog} onClose={handleCloseDialog}>
        <DialogTitle>
          {dialogType === 'addCourse' && '添加新课程'}
          {dialogType === 'editCourse' && '编辑课程'}
          {dialogType === 'addUser' && '添加新用户'}
          {dialogType === 'editUser' && '编辑用户'}
        </DialogTitle>
        <DialogContent>
          {(dialogType === 'addCourse' || dialogType === 'editCourse') && (
            <>
              <TextField
                margin="dense"
                name="course_id"
                label="课程编号"
                fullWidth
                value={formData.course_id || ''}
                onChange={handleInputChange}
                disabled={dialogType === 'editCourse'}
              />
              <TextField
                margin="dense"
                name="course_name"
                label="课程名称"
                fullWidth
                value={formData.course_name || ''}
                onChange={handleInputChange}
              />
              <FormControl fullWidth margin="dense">
                <InputLabel id="dept-select-label">所属院系</InputLabel>
                <Select
                  labelId="dept-select-label"
                  name="dept_id"
                  value={formData.dept_id || ''}
                  onChange={handleInputChange}
                  label="所属院系"
                >
                  {departments.map((dept) => (
                    <MenuItem key={dept.dept_id} value={dept.dept_id}>
                      {dept.dept_name}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              <TextField
                margin="dense"
                name="credit"
                label="学分"
                type="number"
                fullWidth
                value={formData.credit || ''}
                onChange={handleInputChange}
                helperText="请输入1-10之间的整数"
              />
              <TextField
                margin="dense"
                name="credit_hours"
                label="学时"
                type="number"
                fullWidth
                value={formData.credit_hours || ''}
                onChange={handleInputChange}
                helperText="请输入1-255之间的整数，默认为学分×10"
              />
            </>
          )}

          {(dialogType === 'addUser' || dialogType === 'editUser') && (
            <>
              <TextField
                margin="dense"
                name="username"
                label="用户名"
                fullWidth
                value={formData.username || ''}
                onChange={handleInputChange}
                disabled={dialogType === 'editUser'}
              />
              {dialogType === 'addUser' && (
                <TextField
                  margin="dense"
                  name="password"
                  label="密码"
                  type="password"
                  fullWidth
                  value={formData.password || ''}
                  onChange={handleInputChange}
                />
              )}
              <FormControl fullWidth margin="dense">
                <InputLabel id="role-select-label">角色</InputLabel>
                <Select
                  labelId="role-select-label"
                  name="role"
                  value={formData.role || ''}
                  onChange={handleInputChange}
                  label="角色"
                >
                  <MenuItem value="student">学生</MenuItem>
                  <MenuItem value="teacher">教师</MenuItem>
                  <MenuItem value="admin">管理员</MenuItem>
                </Select>
              </FormControl>
              <TextField
                margin="dense"
                name="name"
                label="姓名"
                fullWidth
                value={formData.name || ''}
                onChange={handleInputChange}
              />
              {(formData.role === 'student' || formData.role === 'teacher') && (
                <>
                  <FormControl fullWidth margin="dense">
                    <InputLabel id="dept-select-label">所属院系</InputLabel>
                    <Select
                      labelId="dept-select-label"
                      name="dept_id"
                      value={formData.dept_id || ''}
                      onChange={handleInputChange}
                      label="所属院系"
                    >
                      {departments.map((dept) => (
                        <MenuItem key={dept.dept_id} value={dept.dept_id}>
                          {dept.dept_name}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                  <FormControl fullWidth margin="dense">
                    <InputLabel id="sex-select-label">性别</InputLabel>
                    <Select
                      labelId="sex-select-label"
                      name="sex"
                      value={formData.sex || 'M'}
                      onChange={handleInputChange}
                      label="性别"
                    >
                      <MenuItem value="M">男</MenuItem>
                      <MenuItem value="F">女</MenuItem>
                    </Select>
                  </FormControl>
                  <TextField
                    margin="dense"
                    name="date_of_birth"
                    label="出生日期"
                    type="date"
                    InputLabelProps={{ shrink: true }}
                    fullWidth
                    value={formData.date_of_birth || '2000-01-01'}
                    onChange={handleInputChange}
                  />
                </>
              )}
              {formData.role === 'student' && (
                <TextField
                  margin="dense"
                  name="student_id"
                  label="学号"
                  fullWidth
                  value={formData.student_id || ''}
                  onChange={handleInputChange}
                  helperText="如果不填写，系统将自动生成"
                />
              )}
              {formData.role === 'teacher' && (
                <>
                  <TextField
                    margin="dense"
                    name="staff_id"
                    label="工号"
                    fullWidth
                    value={formData.staff_id || ''}
                    onChange={handleInputChange}
                    helperText="如果不填写，系统将自动生成"
                  />
                  <TextField
                    margin="dense"
                    name="professional_ranks"
                    label="职称"
                    fullWidth
                    value={formData.professional_ranks || ''}
                    onChange={handleInputChange}
                  />
                </>
              )}
            </>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDialog}>取消</Button>
          <Button onClick={handleSubmit} variant="contained">提交</Button>
        </DialogActions>
      </Dialog>

      <Snackbar
        open={openSnackbar}
        autoHideDuration={6000}
        onClose={() => setOpenSnackbar(false)}
      >
        <Alert onClose={() => setOpenSnackbar(false)} severity={severity}>
          {message}
        </Alert>
      </Snackbar>
    </Box>
  );
};

export default AdminDashboard; 