import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Button,
  Alert,
  Snackbar,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Typography,
  TextField,
  Grid,
  Box,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Tabs,
  Tab,
  Chip
} from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import FilterListIcon from '@mui/icons-material/FilterList';
import ClearIcon from '@mui/icons-material/Clear';
import api from '../utils/api';

function TabPanel(props) {
  const { children, value, index, ...other } = props;
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ p: 3 }}>{children}</Box>}
    </div>
  );
}

const CourseList = ({ studentId }) => {
  const [courses, setCourses] = useState([]);
  const [message, setMessage] = useState('');
  const [openSnackbar, setOpenSnackbar] = useState(false);
  const [severity, setSeverity] = useState('success');
  const [currentSemester, setCurrentSemester] = useState('2025-2026-1');
  const [selectedCourses, setSelectedCourses] = useState([]);
  const [tabValue, setTabValue] = useState(0);
  const [departments, setDepartments] = useState([]);
  const [credits, setCredits] = useState([]);

  // 搜索筛选状态
  const [searchFilters, setSearchFilters] = useState({
    course_id: '',
    course_name: '',
    dept_id: '',
    credit: '',
    teacher_name: '',
  });
  
  // 确认对话框状态
  const [confirmDialog, setConfirmDialog] = useState({
    open: false,
    title: '',
    message: '',
    confirmAction: null,
    data: null
  });

  // 获取所有院系
  const fetchDepartments = async () => {
    try {
      const response = await api.get('/api/admin/departments');
      if (response.data.success) {
        setDepartments(response.data.data);
      }
    } catch (error) {
      console.error('Error fetching departments:', error);
    }
  };

  // 获取所有课程学分选项
  const fetchCreditOptions = async () => {
    try {
      const response = await api.get('/api/courses');
      if (response.data.success) {
        // 提取不重复的学分值
        const uniqueCredits = [...new Set(response.data.data.map(course => course.credit))];
        setCredits(uniqueCredits.sort((a, b) => a - b));
      }
    } catch (error) {
      console.error('Error fetching credit options:', error);
    }
  };

  const fetchCourses = async () => {
    try {
      // 构建查询参数
      const params = new URLSearchParams();
      Object.entries(searchFilters).forEach(([key, value]) => {
        if (value) params.append(key, value);
      });
      // 添加学期参数
      params.append('semester', currentSemester);

      const endpoint = Object.values(searchFilters).some(value => value) 
        ? `/api/courses/search?${params.toString()}`
        : '/api/courses';
        
      const response = await api.get(endpoint);
      if (response.data.success) {
        console.log('获取课程数据:', response.data.data);
        // Process courses to handle multiple teachers
        const processedCourses = response.data.data.map(course => {
          // Create teacher list from comma-separated strings
          const teachers = course.teachers ? course.teachers.split(',') : [];
          const staffIds = course.staff_ids ? course.staff_ids.split(',') : [];
          
          // Create teacher objects with name and id
          const teacherList = teachers.map((teacher, index) => ({
            name: teacher,
            staffId: staffIds[index] || ''
          }));
          
          return {
            ...course,
            teacherList
          };
        });
        
        setCourses(processedCourses);
      }
    } catch (error) {
      console.error('Error fetching courses:', error);
      showMessage('获取课程信息失败', 'error');
    }
  };

  const fetchSelectedCourses = async () => {
    if (!studentId) return;
    
    try {
      const response = await api.get(`/api/student/${studentId}/courses`);
      if (response.data.success) {
        console.log('已选课程:', response.data.data);
        // 筛选当前学期的已选课程
        const currentSemesterCourses = response.data.data.filter(
          course => course.semester === currentSemester
        );
        setSelectedCourses(currentSemesterCourses);
      }
    } catch (error) {
      console.error('Error fetching selected courses:', error);
    }
  };

  useEffect(() => {
    fetchDepartments();
    fetchCreditOptions();
  }, []);

  useEffect(() => {
    fetchCourses();
    fetchSelectedCourses();
  }, [studentId, currentSemester]); // eslint-disable-line react-hooks/exhaustive-deps

  const handleSearch = () => {
    fetchCourses();
  };

  const clearSearchFilters = () => {
    setSearchFilters({
      course_id: '',
      course_name: '',
      dept_id: '',
      credit: '',
      teacher_name: '',
    });
    // 清除筛选后重新获取所有课程
    fetchCourses();
  };

  const handleCourseSelection = async (courseId, staffId) => {
    // 打开确认对话框
    setConfirmDialog({
      open: true,
      title: '确认选课',
      message: `你确定要选择这门课程吗？\n选课成功后可以在选课期间内退课，但选课截止后将无法退课。`,
      confirmAction: () => confirmCourseSelection(courseId, staffId),
      data: { courseId, staffId }
    });
  };

  const confirmCourseSelection = async (courseId, staffId) => {
    try {
      const response = await api.post('/api/course-selection', {
        student_id: studentId,
        semester: currentSemester,
        course_id: courseId,
        staff_id: staffId
      });
      
      if (response.data.success) {
        showMessage('选课成功！', 'success');
        fetchSelectedCourses();
      } else {
        showMessage(response.data.message || '选课失败，请稍后重试', 'error');
      }
    } catch (error) {
      if (error.response && error.response.data && error.response.data.message) {
        showMessage(error.response.data.message, 'error');
      } else {
        showMessage('选课失败，请稍后重试', 'error');
      }
    }
    // 关闭确认对话框
    setConfirmDialog({ ...confirmDialog, open: false });
  };

  const handleCourseWithdrawal = (course) => {
    // 打开确认对话框
    setConfirmDialog({
      open: true,
      title: '确认退课',
      message: `你确定要退选 ${course.course_name} 课程吗？\n注意：如果该课程已有成绩记录，将无法退课。`,
      confirmAction: () => confirmCourseWithdrawal(course),
      data: course
    });
  };

  const confirmCourseWithdrawal = async (course) => {
    try {
      const response = await api.delete('/api/course-withdrawal', {
        data: {
          student_id: studentId,
          semester: currentSemester,
          course_id: course.course_id
        }
      });
      
      if (response.data.success) {
        showMessage('退课成功！', 'success');
        fetchSelectedCourses();
      } else {
        showMessage(response.data.message || '退课失败，请稍后重试', 'error');
      }
    } catch (error) {
      if (error.response && error.response.data && error.response.data.message) {
        showMessage(error.response.data.message, 'error');
      } else {
        showMessage('退课失败，请稍后重试', 'error');
      }
    }
    // 关闭确认对话框
    setConfirmDialog({ ...confirmDialog, open: false });
  };

  const handleTabChange = (event, newValue) => {
    setTabValue(newValue);
  };

  const handleFilterChange = (event) => {
    const { name, value } = event.target;
    setSearchFilters({
      ...searchFilters,
      [name]: value
    });
  };

  const showMessage = (msg, sev) => {
    setMessage(msg);
    setSeverity(sev);
    setOpenSnackbar(true);
  };

  // 检查课程是否已经被选择
  const isCourseSelected = (courseId) => {
    return selectedCourses.some(course => course.course_id === courseId);
  };

  const handleCloseDialog = () => {
    setConfirmDialog({
      ...confirmDialog,
      open: false
    });
  };

  const handleConfirmAction = () => {
    if (confirmDialog.confirmAction) {
      confirmDialog.confirmAction();
    }
    handleCloseDialog();
  };

  return (
    <div>
      <Typography variant="h5" gutterBottom>
        {tabValue === 0 ? '可选课程' : '已选课程'}
      </Typography>
      
      <Tabs value={tabValue} onChange={handleTabChange} aria-label="course tabs">
        <Tab label="可选课程" />
        <Tab label="已选课程" />
      </Tabs>
      
      <TabPanel value={tabValue} index={0}>
        {/* 搜索和筛选组件 */}
        <Box sx={{ mb: 3, mt: 2 }}>
          <Typography variant="h6" gutterBottom>
            搜索和筛选
          </Typography>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6} md={3}>
              <TextField
                fullWidth
                label="课程编号"
                name="course_id"
                value={searchFilters.course_id}
                onChange={handleFilterChange}
                variant="outlined"
                size="small"
              />
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
              <TextField
                fullWidth
                label="课程名称"
                name="course_name"
                value={searchFilters.course_name}
                onChange={handleFilterChange}
                variant="outlined"
                size="small"
              />
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
              <FormControl fullWidth size="small">
                <InputLabel>院系</InputLabel>
                <Select
                  name="dept_id"
                  value={searchFilters.dept_id}
                  onChange={handleFilterChange}
                  label="院系"
                >
                  <MenuItem value=""><em>全部</em></MenuItem>
                  {departments.map(dept => (
                    <MenuItem key={dept.dept_id} value={dept.dept_id}>
                      {dept.dept_name}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
              <FormControl fullWidth size="small">
                <InputLabel>学分</InputLabel>
                <Select
                  name="credit"
                  value={searchFilters.credit}
                  onChange={handleFilterChange}
                  label="学分"
                >
                  <MenuItem value=""><em>全部</em></MenuItem>
                  {credits.map(credit => (
                    <MenuItem key={credit} value={credit}>
                      {credit}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
              <TextField
                fullWidth
                label="教师姓名"
                name="teacher_name"
                value={searchFilters.teacher_name}
                onChange={handleFilterChange}
                variant="outlined"
                size="small"
              />
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
              <FormControl fullWidth size="small">
                <InputLabel>学期</InputLabel>
                <Select
                  value={currentSemester}
                  onChange={(e) => setCurrentSemester(e.target.value)}
                  label="学期"
                >
                  <MenuItem value="2023-2024-1">2023-2024 第一学期</MenuItem>
                  <MenuItem value="2023-2024-2">2023-2024 第二学期</MenuItem>
                  <MenuItem value="2025-2026-1">2025-2026 第一学期</MenuItem>
                  <MenuItem value="2025-2026-2">2025-2026 第二学期</MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12} sm={6} md={6}>
              <Box sx={{ display: 'flex', gap: 1 }}>
                <Button 
                  variant="contained" 
                  onClick={handleSearch}
                  startIcon={<SearchIcon />}
                >
                  搜索
                </Button>
                <Button 
                  variant="outlined" 
                  onClick={clearSearchFilters}
                  startIcon={<ClearIcon />}
                >
                  清除筛选
                </Button>
              </Box>
            </Grid>
          </Grid>
        </Box>
        
        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 650 }}>
            <TableHead>
              <TableRow>
                <TableCell>课程编号</TableCell>
                <TableCell>课程名称</TableCell>
                <TableCell>开课院系</TableCell>
                <TableCell>学分</TableCell>
                <TableCell>授课教师</TableCell>
                <TableCell>操作</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {courses.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={6} align="center">
                    暂无课程数据
                  </TableCell>
                </TableRow>
              ) : courses.map((course) => (
                <TableRow key={course.course_id}>
                  <TableCell>{course.course_id}</TableCell>
                  <TableCell>{course.course_name}</TableCell>
                  <TableCell>{course.dept_name}</TableCell>
                  <TableCell>{course.credit}</TableCell>
                  <TableCell>
                    {course.teacherList && course.teacherList.length > 0 ? (
                      <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                        {course.teacherList.map((teacher, index) => (
                          <Chip 
                            key={index} 
                            label={teacher.name} 
                            size="small" 
                            color="primary" 
                            variant="outlined"
                            onClick={
                              !isCourseSelected(course.course_id) ? 
                                () => handleCourseSelection(course.course_id, teacher.staffId) : 
                                undefined
                            }
                          />
                        ))}
                      </Box>
                    ) : (
                      '无教师信息'
                    )}
                  </TableCell>
                  <TableCell>
                    {isCourseSelected(course.course_id) ? (
                      <Chip color="success" label="已选" />
                    ) : course.teacherList && course.teacherList.length > 0 ? (
                      <Button 
                        variant="contained" 
                        color="primary"
                        size="small"
                        onClick={() => handleCourseSelection(
                          course.course_id, 
                          course.teacherList[0].staffId
                        )}
                      >
                        选课
                      </Button>
                    ) : (
                      <Button variant="contained" disabled>
                        选课
                      </Button>
                    )}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </TabPanel>
      
      <TabPanel value={tabValue} index={1}>
        {selectedCourses.length === 0 ? (
          <Box sx={{ my: 2 }}>
            <Alert severity="info">
              {currentSemester}学期暂无已选课程
            </Alert>
          </Box>
        ) : (
          <TableContainer component={Paper}>
            <Table sx={{ minWidth: 650 }}>
              <TableHead>
                <TableRow>
                  <TableCell>学期</TableCell>
                  <TableCell>课程编号</TableCell>
                  <TableCell>课程名称</TableCell>
                  <TableCell>教师姓名</TableCell>
                  <TableCell>学分</TableCell>
                  <TableCell>操作</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {selectedCourses.map((course) => (
                  <TableRow key={course.id}>
                    <TableCell>{course.semester}</TableCell>
                    <TableCell>{course.course_id}</TableCell>
                    <TableCell>{course.course_name}</TableCell>
                    <TableCell>{course.teacher_name}</TableCell>
                    <TableCell>{course.credit}</TableCell>
                    <TableCell>
                      <Button 
                        variant="outlined" 
                        color="error"
                        onClick={() => handleCourseWithdrawal(course)}
                      >
                        退课
                      </Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        )}
      </TabPanel>
      
      {/* 确认对话框 */}
      <Dialog
        open={confirmDialog.open}
        onClose={handleCloseDialog}
      >
        <DialogTitle>{confirmDialog.title}</DialogTitle>
        <DialogContent>
          <DialogContentText>
            {confirmDialog.message}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDialog} color="primary">
            取消
          </Button>
          <Button onClick={handleConfirmAction} color="primary" variant="contained">
            确定
          </Button>
        </DialogActions>
      </Dialog>
      
      <Snackbar 
        open={openSnackbar}
        autoHideDuration={6000}
        onClose={() => setOpenSnackbar(false)}
      >
        <Alert onClose={() => setOpenSnackbar(false)} severity={severity}>
          {message}
        </Alert>
      </Snackbar>
    </div>
  );
};

export default CourseList; 