import React, { useState, useEffect } from 'react';
import {
  Box,
  Paper,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Grid,
  Card,
  CardContent
} from '@mui/material';
import api from '../utils/api';

const Statistics = ({ role, userId }) => {
  const [stats, setStats] = useState(null);

  const fetchStatistics = async () => {
    try {
      let response;
      if (role === 'admin') {
        response = await api.get('/api/admin/statistics');
      } else if (role === 'teacher') {
        response = await api.get(`/api/teacher/${userId}/statistics`);
      } else if (role === 'student') {
        response = await api.get(`/api/student/${userId}/statistics`);
      }

      if (response && response.data.success) {
        console.log("Statistics data:", response.data.data);
        setStats(response.data.data);
      }
    } catch (error) {
      console.error('Error fetching statistics:', error);
    }
  };

  useEffect(() => {
    fetchStatistics();
  }, [role, userId]); // eslint-disable-line react-hooks/exhaustive-deps

  const renderAdminStats = () => (
    <Grid container spacing={3}>
      <Grid item xs={12}>
        <Typography variant="h6" gutterBottom>
          Department Statistics
        </Typography>
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Department</TableCell>
                <TableCell>Courses</TableCell>
                <TableCell>Students</TableCell>
                <TableCell>Teachers</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {stats.department_statistics && stats.department_statistics.map((dept) => (
                <TableRow key={dept.dept_name}>
                  <TableCell>{dept.dept_name}</TableCell>
                  <TableCell>{dept.course_count}</TableCell>
                  <TableCell>{dept.student_count}</TableCell>
                  <TableCell>{dept.teacher_count}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Grid>
      <Grid item xs={12}>
        <Typography variant="h6" gutterBottom>
          Course Statistics
        </Typography>
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Course</TableCell>
                <TableCell>Students</TableCell>
                <TableCell>Average Score</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {stats.course_statistics && stats.course_statistics.map((course) => (
                <TableRow key={course.course_id}>
                  <TableCell>{course.course_name}</TableCell>
                  <TableCell>{course.student_count}</TableCell>
                  <TableCell>{parseFloat(course.avg_score || 0).toFixed(2)}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Grid>
    </Grid>
  );

  const renderTeacherStats = () => (
    <Grid container spacing={3}>
      <Grid item xs={12}>
        <Typography variant="h6" gutterBottom>
          Course Performance Statistics
        </Typography>
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Course</TableCell>
                <TableCell>Semester</TableCell>
                <TableCell>Students</TableCell>
                <TableCell>Avg Usual</TableCell>
                <TableCell>Avg Final</TableCell>
                <TableCell>Avg Total</TableCell>
                <TableCell>Min Score</TableCell>
                <TableCell>Max Score</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {stats.course_statistics && stats.course_statistics.map((course) => (
                <TableRow key={`${course.course_id}-${course.semester}`}>
                  <TableCell>{course.course_name}</TableCell>
                  <TableCell>{course.semester}</TableCell>
                  <TableCell>{course.student_count}</TableCell>
                  <TableCell>{parseFloat(course.avg_usual_score || 0).toFixed(2)}</TableCell>
                  <TableCell>{parseFloat(course.avg_final_score || 0).toFixed(2)}</TableCell>
                  <TableCell>{parseFloat(course.avg_total_score || 0).toFixed(2)}</TableCell>
                  <TableCell>{course.min_score}</TableCell>
                  <TableCell>{course.max_score}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Grid>
    </Grid>
  );

  const renderStudentStats = () => {
    // 确保gpa是有效的数字值
    const gpa = stats.gpa !== null && stats.gpa !== undefined && !isNaN(stats.gpa) 
      ? parseFloat(stats.gpa) 
      : 0;
    
    return (
      <Grid container spacing={3}>
        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                Overall GPA
              </Typography>
              <Typography variant="h3" color="primary">
                {gpa.toFixed(2)}
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12}>
          <Typography variant="h6" gutterBottom>
            Semester Performance
          </Typography>
          <TableContainer component={Paper}>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>Semester</TableCell>
                  <TableCell>Courses</TableCell>
                  <TableCell>Total Credits</TableCell>
                  <TableCell>Earned Credits</TableCell>
                  <TableCell>Average Score</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {stats.semester_statistics && stats.semester_statistics.map((semester) => (
                  <TableRow key={semester.semester}>
                    <TableCell>{semester.semester}</TableCell>
                    <TableCell>{semester.course_count}</TableCell>
                    <TableCell>{semester.total_credits}</TableCell>
                    <TableCell>{semester.earned_credits}</TableCell>
                    <TableCell>{parseFloat(semester.avg_score || 0).toFixed(2)}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Grid>
      </Grid>
    );
  };

  if (!stats) {
    return <Typography>Loading statistics...</Typography>;
  }

  return (
    <Box sx={{ p: 3 }}>
      <Typography variant="h5" gutterBottom>
        Statistics and Analytics
      </Typography>
      {role === 'admin' && renderAdminStats()}
      {role === 'teacher' && renderTeacherStats()}
      {role === 'student' && renderStudentStats()}
    </Box>
  );
};

export default Statistics; 