import React, { useState, useEffect } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Typography,
  Box,
  Alert,
  Snackbar,
  Select,
  MenuItem,
  FormControl,
  InputLabel
} from '@mui/material';
import api from '../utils/api';

const TeacherCourses = ({ staffId }) => {
  const [courses, setCourses] = useState([]);
  const [selectedCourse, setSelectedCourse] = useState(null);
  const [students, setStudents] = useState([]);
  const [openDialog, setOpenDialog] = useState(false);
  const [message, setMessage] = useState('');
  const [openSnackbar, setOpenSnackbar] = useState(false);
  const [severity, setSeverity] = useState('success');
  const [semesters, setSemesters] = useState([]);
  const [selectedSemester, setSelectedSemester] = useState('');

  // 获取教师所有学期
  const fetchTeacherSemesters = async () => {
    try {
      const response = await api.get(`/api/teacher/${staffId}/semesters`);
      if (response.data.success && response.data.data.length > 0) {
        const semestersData = response.data.data.map(s => s.semester);
        setSemesters(semestersData);
        // 默认选择第一个学期
        setSelectedSemester(semestersData[0]);
      }
    } catch (error) {
      showMessage('获取学期数据失败', 'error');
    }
  };

  const fetchTeacherCourses = async (semester) => {
    try {
      if (!semester) return;
      
      const response = await api.get(`/api/teacher/${staffId}/semester/${semester}/courses`);
      if (response.data.success) {
        setCourses(response.data.data);
      }
    } catch (error) {
      showMessage('获取课程数据失败', 'error');
    }
  };

  useEffect(() => {
    fetchTeacherSemesters();
  }, [staffId]); // eslint-disable-line react-hooks/exhaustive-deps

  useEffect(() => {
    if (selectedSemester) {
      fetchTeacherCourses(selectedSemester);
    }
  }, [selectedSemester, staffId]); // eslint-disable-line react-hooks/exhaustive-deps

  const handleSemesterChange = (event) => {
    setSelectedSemester(event.target.value);
  };

  const handleViewStudents = async (courseId, semester) => {
    try {
      const response = await api.get(`/api/teacher/course/${courseId}/semester/${semester}/students`);
      if (response.data.success) {
        setStudents(response.data.data);
        setSelectedCourse({ courseId, semester });
        setOpenDialog(true);
      }
    } catch (error) {
      showMessage('获取学生名单失败', 'error');
    }
  };

  const handleGradeChange = (studentId, field, value) => {
    // 添加成绩验证
    let validValue = value;
    
    // 如果不是数字或为空，允许为空但不允许非数字
    if (value !== '' && isNaN(value)) {
      return; // 不更新非数字的输入
    }
    
    // 如果是数字，确保在0-100范围内
    if (value !== '' && !isNaN(value)) {
      const numValue = parseFloat(value);
      if (numValue < 0) validValue = 0;
      if (numValue > 100) validValue = 100;
    }
    
    setStudents(students.map(student => {
      if (student.student_id === studentId) {
        return { ...student, [field]: validValue };
      }
      return student;
    }));
  };

  const handleSaveGrades = async () => {
    try {
      // 确认是否有未完成的成绩
      const incompleteStudents = students.filter(
        student => (student.usual_score === null || student.usual_score === '') && 
                  (student.final_score === null || student.final_score === '')
      );
      
      if (incompleteStudents.length > 0 && 
          !window.confirm('有学生的成绩尚未完全填写，确定保存当前成绩吗？')) {
        return;
      }
      
      const response = await api.post('/api/teacher/grades', {
        course_id: selectedCourse.courseId,
        semester: selectedCourse.semester,
        grades: students.map(student => ({
          student_id: student.student_id,
          usual_score: student.usual_score === '' ? null : student.usual_score,
          final_score: student.final_score === '' ? null : student.final_score
        }))
      });

      if (response.data.success) {
        showMessage('成绩保存成功', 'success');
        setOpenDialog(false);
      }
    } catch (error) {
      if (error.response && error.response.data && error.response.data.message) {
        showMessage(error.response.data.message, 'error');
      } else {
        showMessage('保存成绩失败', 'error');
      }
    }
  };

  const showMessage = (msg, sev) => {
    setMessage(msg);
    setSeverity(sev);
    setOpenSnackbar(true);
  };

  return (
    <Box>
      <Box sx={{ mb: 3, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <Typography variant="h6" gutterBottom>
          我的课程
        </Typography>
        <FormControl sx={{ width: 200 }}>
          <InputLabel id="semester-select-label">学期</InputLabel>
          <Select
            labelId="semester-select-label"
            id="semester-select"
            value={selectedSemester}
            label="学期"
            onChange={handleSemesterChange}
          >
            {semesters.map((semester) => (
              <MenuItem key={semester} value={semester}>
                {semester}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      </Box>
      
      {courses.length > 0 ? (
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>课程编号</TableCell>
                <TableCell>课程名称</TableCell>
                <TableCell>所属院系</TableCell>
                <TableCell>操作</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {courses.map((course) => (
                <TableRow key={`${course.course_id}-${course.semester}`}>
                  <TableCell>{course.course_id}</TableCell>
                  <TableCell>{course.course_name}</TableCell>
                  <TableCell>{course.dept_name}</TableCell>
                  <TableCell>
                    <Button
                      variant="contained"
                      onClick={() => handleViewStudents(course.course_id, course.semester)}
                    >
                      查看学生名单及成绩
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      ) : (
        <Alert severity="info">所选学期暂无课程。</Alert>
      )}

      <Dialog open={openDialog} onClose={() => setOpenDialog(false)} maxWidth="md" fullWidth>
        <DialogTitle>
          学生成绩管理 - {selectedCourse?.courseId} ({selectedCourse?.semester})
        </DialogTitle>
        <DialogContent>
          {students.length > 0 ? (
            <TableContainer>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>学号</TableCell>
                    <TableCell>姓名</TableCell>
                    <TableCell>平时成绩（40%）</TableCell>
                    <TableCell>期末成绩（60%）</TableCell>
                    <TableCell>总评成绩</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {students.map((student) => (
                    <TableRow key={student.student_id}>
                      <TableCell>{student.student_id}</TableCell>
                      <TableCell>{student.name}</TableCell>
                      <TableCell>
                        <TextField
                          type="number"
                          size="small"
                          inputProps={{ min: 0, max: 100, step: "0.1" }}
                          value={student.usual_score !== null ? student.usual_score : ''}
                          onChange={(e) => handleGradeChange(student.student_id, 'usual_score', e.target.value)}
                          helperText="0-100"
                        />
                      </TableCell>
                      <TableCell>
                        <TextField
                          type="number"
                          size="small"
                          inputProps={{ min: 0, max: 100, step: "0.1" }}
                          value={student.final_score !== null ? student.final_score : ''}
                          onChange={(e) => handleGradeChange(student.student_id, 'final_score', e.target.value)}
                          helperText="0-100"
                        />
                      </TableCell>
                      <TableCell>
                        {student.total_score !== null ? student.total_score : (
                          student.usual_score !== null && student.final_score !== null ? 
                          (parseFloat(student.usual_score) * 0.4 + parseFloat(student.final_score) * 0.6).toFixed(2) : 
                          '尚未计算'
                        )}
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          ) : (
            <Alert severity="info" sx={{ mt: 2 }}>
              这门课程暂时没有学生选课。
            </Alert>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setOpenDialog(false)} color="inherit">
            取消
          </Button>
          {students.length > 0 && (
            <>
              <Button 
                variant="contained" 
                color="primary" 
                onClick={handleSaveGrades}
              >
                保存成绩
              </Button>
            </>
          )}
        </DialogActions>
      </Dialog>

      <Snackbar
        open={openSnackbar}
        autoHideDuration={6000}
        onClose={() => setOpenSnackbar(false)}
      >
        <Alert onClose={() => setOpenSnackbar(false)} severity={severity}>
          {message}
        </Alert>
      </Snackbar>
    </Box>
  );
};

export default TeacherCourses; 