import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  Paper,
  Grid,
  TextField,
  Button,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Alert,
  Snackbar,
  IconButton,
  Divider
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import AddIcon from '@mui/icons-material/Add';
import api from '../utils/api';

const AdminTeacherCourses = () => {
  const [teachers, setTeachers] = useState([]);
  const [courses, setCourses] = useState([]);
  const [semesters, setSemesters] = useState([]);
  const [selectedTeacher, setSelectedTeacher] = useState('');
  const [selectedCourse, setSelectedCourse] = useState('');
  const [selectedSemester, setSelectedSemester] = useState('');
  const [newSemesterName, setNewSemesterName] = useState('');
  const [classTime, setClassTime] = useState('');
  const [teacherCourses, setTeacherCourses] = useState([]);
  const [openDialog, setOpenDialog] = useState(false);
  const [snackbar, setSnackbar] = useState({
    open: false,
    message: '',
    severity: 'success'
  });

  // 加载教师、课程和学期数据
  useEffect(() => {
    fetchTeachers();
    fetchCourses();
    fetchSemesters();
  }, []);

  // 获取所有教师
  const fetchTeachers = async () => {
    try {
      const response = await api.get('/api/admin/teachers');
      if (response.data.success) {
        setTeachers(response.data.data);
      }
    } catch (error) {
      showSnackbar('获取教师信息失败', 'error');
    }
  };

  // 获取所有课程
  const fetchCourses = async () => {
    try {
      const response = await api.get('/api/admin/courses');
      if (response.data.success) {
        setCourses(response.data.data);
      }
    } catch (error) {
      showSnackbar('获取课程信息失败', 'error');
    }
  };

  // 获取所有学期
  const fetchSemesters = async () => {
    try {
      const response = await api.get('/api/admin/semesters');
      if (response.data.success) {
        setSemesters(response.data.data);
        if (response.data.data.length > 0) {
          setSelectedSemester(response.data.data[0].semester);
        }
      }
    } catch (error) {
      showSnackbar('获取学期信息失败', 'error');
    }
  };

  // 获取特定教师的课程
  const fetchTeacherCourses = async (staffId) => {
    if (!staffId) return;
    try {
      const response = await api.get(`/api/teacher/${staffId}/courses`);
      if (response.data.success) {
        setTeacherCourses(response.data.data);
      }
    } catch (error) {
      showSnackbar('获取教师课程信息失败', 'error');
    }
  };

  // 处理教师选择变更
  const handleTeacherChange = (event) => {
    const staffId = event.target.value;
    setSelectedTeacher(staffId);
    fetchTeacherCourses(staffId);
  };

  // 处理学期选择变更
  const handleSemesterChange = (event) => {
    setSelectedSemester(event.target.value);
  };

  // 处理课程选择变更
  const handleCourseChange = (event) => {
    setSelectedCourse(event.target.value);
  };

  // 创建新学期
  const handleCreateSemester = async () => {
    if (!newSemesterName.trim()) {
      showSnackbar('请输入有效的学期名称', 'error');
      return;
    }

    try {
      const response = await api.post('/api/admin/semesters', {
        semester: newSemesterName
      });

      if (response.data.success) {
        showSnackbar('新学期创建成功', 'success');
        fetchSemesters();
        setNewSemesterName('');
      } else {
        showSnackbar(response.data.message || '创建学期失败', 'error');
      }
    } catch (error) {
      showSnackbar('创建学期时出错', 'error');
    }
  };

  // 分配课程给教师
  const handleAssignCourse = async () => {
    if (!selectedTeacher || !selectedCourse || !selectedSemester || !classTime) {
      showSnackbar('请填写完整信息', 'error');
      return;
    }

    try {
      const response = await api.post('/api/admin/assign-course', {
        staff_id: selectedTeacher,
        course_id: selectedCourse,
        semester: selectedSemester,
        class_time: classTime
      });

      if (response.data.success) {
        showSnackbar('课程分配成功', 'success');
        fetchTeacherCourses(selectedTeacher);
        setSelectedCourse('');
        setClassTime('');
      } else {
        showSnackbar(response.data.message || '课程分配失败', 'error');
      }
    } catch (error) {
      showSnackbar('分配课程时出错', 'error');
    }
  };

  // 移除教师课程
  const handleRemoveCourse = async (courseId, semester) => {
    if (window.confirm(`确定要移除教师的 ${courseId} 课程（${semester}学期）吗？此操作将影响课程安排。`)) {
      confirmRemoveCourse(courseId, semester);
    }
  };

  // 确认移除课程
  const confirmRemoveCourse = async (courseId, semester) => {
    if (!selectedTeacher || !courseId || !semester) {
      showSnackbar('缺少必要信息', 'error');
      return;
    }

    try {
      const response = await api.delete('/api/admin/remove-course', {
        data: {
          staff_id: selectedTeacher,
          course_id: courseId,
          semester: semester
        }
      });

      if (response.data.success) {
        showSnackbar('课程已成功移除', 'success');
        fetchTeacherCourses(selectedTeacher);
      } else {
        showSnackbar(response.data.message || '移除课程失败', 'error');
      }
    } catch (error) {
      if (error.response && error.response.data && error.response.data.message) {
        showSnackbar(error.response.data.message, 'error');
      } else {
        showSnackbar('移除课程时出错', 'error');
      }
    }
  };

  // 显示消息提示
  const showSnackbar = (message, severity) => {
    setSnackbar({
      open: true,
      message,
      severity
    });
  };

  // 关闭消息提示
  const handleCloseSnackbar = () => {
    setSnackbar({
      ...snackbar,
      open: false
    });
  };

  return (
    <Box sx={{ flexGrow: 1, mt: 2 }}>
      <Typography variant="h6" gutterBottom>
        教师课程管理
      </Typography>
      <Divider sx={{ mb: 2 }} />

      <Grid container spacing={2}>
        {/* 左侧区域 - 教师选择 */}
        <Grid item xs={12} md={3}>
          <Paper sx={{ p: 2, height: '100%' }}>
            <Typography variant="subtitle1" gutterBottom>
              选择教师
            </Typography>
            <FormControl fullWidth margin="normal">
              <InputLabel id="teacher-select-label">教师</InputLabel>
              <Select
                labelId="teacher-select-label"
                value={selectedTeacher}
                onChange={handleTeacherChange}
                label="教师"
              >
                <MenuItem value=""><em>请选择</em></MenuItem>
                {teachers.map((teacher) => (
                  <MenuItem key={teacher.staff_id} value={teacher.staff_id}>
                    {teacher.name} ({teacher.staff_id})
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Paper>
        </Grid>

        {/* 中间区域 - 学期管理 */}
        <Grid item xs={12} md={3}>
          <Paper sx={{ p: 2 }}>
            <Typography variant="subtitle1" gutterBottom>
              学期管理
            </Typography>
            <FormControl fullWidth margin="normal">
              <InputLabel id="semester-select-label">学期</InputLabel>
              <Select
                labelId="semester-select-label"
                value={selectedSemester}
                onChange={handleSemesterChange}
                label="学期"
              >
                {semesters.map((semester) => (
                  <MenuItem key={semester.semester} value={semester.semester}>
                    {semester.semester} {semester.is_current ? '(当前学期)' : ''}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>

            <Box sx={{ mt: 2 }}>
              <Typography variant="subtitle2" gutterBottom>
                创建新学期
              </Typography>
              <Grid container spacing={1}>
                <Grid item xs={8}>
                  <TextField
                    fullWidth
                    size="small"
                    label="学期名称"
                    placeholder="例如: 2025-2026-1"
                    value={newSemesterName}
                    onChange={(e) => setNewSemesterName(e.target.value)}
                    helperText="格式: 学年-学期，例如 2025-2026-1"
                  />
                </Grid>
                <Grid item xs={4}>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={handleCreateSemester}
                    fullWidth
                    sx={{ height: '40px' }}
                  >
                    创建
                  </Button>
                </Grid>
              </Grid>
            </Box>
          </Paper>
        </Grid>

        {/* 右侧区域 - 课程分配 */}
        <Grid item xs={12} md={6}>
          <Paper sx={{ p: 2 }}>
            <Typography variant="subtitle1" gutterBottom>
              分配课程
            </Typography>
            {selectedTeacher ? (
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <FormControl fullWidth margin="normal">
                    <InputLabel id="course-select-label">课程</InputLabel>
                    <Select
                      labelId="course-select-label"
                      value={selectedCourse}
                      onChange={handleCourseChange}
                      label="课程"
                    >
                      <MenuItem value=""><em>请选择</em></MenuItem>
                      {courses.map((course) => (
                        <MenuItem key={course.course_id} value={course.course_id}>
                          {course.course_id} - {course.course_name}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    label="上课时间"
                    type="datetime-local"
                    value={classTime}
                    onChange={(e) => setClassTime(e.target.value)}
                    InputLabelProps={{ shrink: true }}
                  />
                </Grid>
                <Grid item xs={12}>
                  <Button
                    variant="contained"
                    color="primary"
                    startIcon={<AddIcon />}
                    onClick={handleAssignCourse}
                    disabled={!selectedCourse || !selectedSemester || !classTime}
                  >
                    分配课程
                  </Button>
                </Grid>
              </Grid>
            ) : (
              <Alert severity="info">请先选择一名教师</Alert>
            )}
          </Paper>
        </Grid>

        {/* 教师课程列表 */}
        <Grid item xs={12}>
          <Paper sx={{ p: 2 }}>
            <Typography variant="subtitle1" gutterBottom>
              教师的课程
            </Typography>
            {selectedTeacher ? (
              teacherCourses.length > 0 ? (
                <TableContainer>
                  <Table size="small">
                    <TableHead>
                      <TableRow>
                        <TableCell>课程编号</TableCell>
                        <TableCell>课程名称</TableCell>
                        <TableCell>学期</TableCell>
                        <TableCell>院系</TableCell>
                        <TableCell>操作</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {teacherCourses.map((course) => (
                        <TableRow key={`${course.course_id}-${course.semester}`}>
                          <TableCell>{course.course_id}</TableCell>
                          <TableCell>{course.course_name}</TableCell>
                          <TableCell>{course.semester}</TableCell>
                          <TableCell>{course.dept_name}</TableCell>
                          <TableCell>
                            <IconButton
                              color="error"
                              size="small"
                              onClick={() => handleRemoveCourse(course.course_id, course.semester)}
                            >
                              <DeleteIcon />
                            </IconButton>
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </TableContainer>
              ) : (
                <Alert severity="info">该教师暂无分配的课程</Alert>
              )
            ) : (
              <Alert severity="info">请先选择一名教师查看其课程</Alert>
            )}
          </Paper>
        </Grid>
      </Grid>

      <Snackbar
        open={snackbar.open}
        autoHideDuration={6000}
        onClose={handleCloseSnackbar}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert onClose={handleCloseSnackbar} severity={snackbar.severity}>
          {snackbar.message}
        </Alert>
      </Snackbar>
    </Box>
  );
};

export default AdminTeacherCourses; 