import React, { useState, useEffect } from 'react';
import { Container, Box, AppBar, Toolbar, Typography, CssBaseline, Button, Tab, Tabs } from '@mui/material';
import './App.css';
import CourseList from './components/CourseList';
import StudentCourses from './components/StudentCourses';
import TeacherCourses from './components/TeacherCourses';
import AdminDashboard from './components/AdminDashboard';
import Statistics from './components/Statistics';
import Login from './components/Login/Login';
import api from './utils/api';

function TabPanel(props) {
  const { children, value, index, ...other } = props;
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ p: 3 }}>{children}</Box>}
    </div>
  );
}

function App() {
  const [user, setUser] = useState(null);
  const [activeTab, setActiveTab] = useState(0);

  useEffect(() => {
    // Check for existing token
    const token = localStorage.getItem('token');
    if (token) {
      api.defaults.headers.common['Authorization'] = `Bearer ${token}`;
      // You might want to validate the token here
    }
  }, []);

  const handleLogin = (userData) => {
    setUser(userData);
  };

  const handleLogout = () => {
    localStorage.removeItem('token');
    delete api.defaults.headers.common['Authorization'];
    setUser(null);
  };

  const handleTabChange = (event, newValue) => {
    setActiveTab(newValue);
  };

  if (!user) {
    return <Login onLogin={handleLogin} />;
  }

  return (
    <>
      <CssBaseline />
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6" sx={{ flexGrow: 1 }}>
            教务选课系统
          </Typography>
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Typography variant="subtitle1" sx={{ mr: 2 }}>
              欢迎，{user.name}
            </Typography>
            <Button color="inherit" onClick={handleLogout}>
              退出登录
            </Button>
          </Box>
        </Toolbar>
      </AppBar>
      <Container maxWidth="lg">
        <Box sx={{ mt: 4 }}>
          {user.role === 'student' && (
            <>
              <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                <Tabs value={activeTab} onChange={handleTabChange}>
                  <Tab label="我的课程" />
                  <Tab label="选课中心" />
                  <Tab label="统计信息" />
                </Tabs>
              </Box>
              
              <TabPanel value={activeTab} index={0}>
                <StudentCourses studentId={user.role_id} />
              </TabPanel>
              
              <TabPanel value={activeTab} index={1}>
                <CourseList studentId={user.role_id} />
              </TabPanel>
              
              <TabPanel value={activeTab} index={2}>
                <Statistics role="student" userId={user.role_id} />
              </TabPanel>
            </>
          )}
          
          {user.role === 'teacher' && (
            <>
              <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                <Tabs value={activeTab} onChange={handleTabChange}>
                  <Tab label="我的课程" />
                  <Tab label="统计信息" />
                </Tabs>
              </Box>
              
              <TabPanel value={activeTab} index={0}>
                <TeacherCourses staffId={user.role_id} />
              </TabPanel>
              
              <TabPanel value={activeTab} index={1}>
                <Statistics role="teacher" userId={user.role_id} />
              </TabPanel>
            </>
          )}
          
          {user.role === 'admin' && (
            <>
              <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                <Tabs value={activeTab} onChange={handleTabChange}>
                  <Tab label="系统管理" />
                  <Tab label="统计信息" />
                </Tabs>
              </Box>
              
              <TabPanel value={activeTab} index={0}>
                <AdminDashboard />
              </TabPanel>
              
              <TabPanel value={activeTab} index={1}>
                <Statistics role="admin" userId={user.role_id} />
              </TabPanel>
            </>
          )}
        </Box>
      </Container>
    </>
  );
}

export default App;
